import { render } from '@testing-library/preact';
import { expect } from 'chai';
import { Subheading } from '../Subheading';
import { Text } from '../../UNSAFE_Text';
import { Link } from '../../UNSAFE_Link';
import userEvent from '@testing-library/user-event';

describe('Test Subheading component', () => {
  it('default Subheading', async function () {
    const content = render(<Subheading as="h1">Hello world</Subheading>);
    const h1 = await content.findByText('Hello world');
    const classList = h1.classList;
    expect(h1).not.null;
    expect(classList.length).equals(2);
  });

  it('subheading text lineClamp applies css class and lineClamp value', async () => {
    const content = render(
      <Subheading as="h1">
        <Text lineClamp={4}>Hello world with lineClamp</Text>
      </Subheading>
    );
    const span = await content.findByText('Hello world with lineClamp');
    const lineClampValue = (span as HTMLElement).parentElement?.style.webkitLineClamp;
    // @ts-ignore
    const classList = span.parentElement?.classList;
    expect(classList?.length).equals(6);
    expect(lineClampValue).equals('4');
  });
  it('Subheading text truncation applies css class', async () => {
    const content = render(
      <Subheading as="h1">
        <Text truncation="ellipsis">Hello world</Text>
      </Subheading>
    );
    const span = await content.findByText('Hello world');
    // @ts-ignore
    const classList = span.parentElement?.classList;
    expect(classList?.length).equals(6);
  });
  it('Subheading link applies default CSS classes', async function () {
    const content = render(
      <Subheading as="h1">
        <Link href="#">Link</Link>
      </Subheading>
    );
    const firstElement = await content.findByText('Link');
    // @ts-ignore
    const classList = firstElement.classList;
    expect(classList.length).equals(2);
  });

  it('Subheading link variant selected', async function () {
    const content = render(
      <Subheading as="h1">
        <Link href="#" variant="primary">
          Link
        </Link>
      </Subheading>
    );
    const firstElement = await content.findByText('Link');
    // @ts-ignore
    const classList = firstElement.classList;
    expect(classList.length).equals(3);
  });

  it('Subheading link event', async function () {
    let count = 0;
    const increment = () => count++;
    const content = render(
      <Subheading as="h1">
        <Link href="#" onClick={increment}>
          Link
        </Link>
      </Subheading>
    );
    const link = await content.findByText('Link');
    expect(link).not.null;
    userEvent.click(link);
    expect(count).equals(1);
  });

  it('has id property', async function () {
    const content = render(
      <Subheading as="h1" id="subheading1">
        Subheading 1
      </Subheading>
    );
    const subheading = await content.findByText('Subheading 1');
    const id = subheading.getAttribute('id');
    expect(id).to.equal('subheading1');
  });
});
